.home-wrap{
  /*background-color: #EBE5E5;*/
}
.right-section{
  position: relative;
  height: 100%;
}
.left-section{
  position: relative;
  height: 100%;
}
.left-section img{
    border-radius: 50%;
    -webkit-filter: grayscale(100%) blur(3px);
            filter: grayscale(100%) blur(3px);
    width: 35rem;
    height: 35rem;
    -o-object-fit: cover;
       object-fit: cover;
    margin-top: 30px;
}
.left-section img:hover{
    -webkit-filter: none;
            filter: none;
}
.salutation{
  font-size: 3rem;
  text-align: left;
}
.brief-about-me{
  text-align: left;
  width: 80%;
  font-size: 2rem;
  font-weight: 200;
  margin-bottom: 15px;
}
.res-wor-ski{
  margin-top: 4rem;
}
.res-wor-ski .r-w-s{
  /*border-radius: 50%;*/
  width: 100px;
  line-height: 100px;
  float: left;
  margin-right: 3rem;
  height: 100px;
  background-color: #86BAA1;
  background-color: #86BAA1;
  -webkit-transform: rotate(14deg);
      -ms-transform: rotate(14deg);
          transform: rotate(14deg);
  cursor: pointer;
  color: white;
  -webkit-transition: .1s transform linear;
  -o-transition: .1s transform linear;
  transition: .1s transform linear;
}
.res-wor-ski .r-w-s:nth-child(3){
  margin-right: 0;
}
.res-wor-ski .r-w-s:hover{
  -webkit-transform: scale(1.1) rotate(14deg);
      -ms-transform: scale(1.1) rotate(14deg);
          transform: scale(1.1) rotate(14deg);
  /*background-color: #bd8d36;*/
}
.me-container2{
  position: absolute;
  left: 0;
  top: 48%;
  -webkit-transform: translate(0,-50%);
      -ms-transform: translate(0,-50%);
          transform: translate(0,-50%);
}
.me-container{
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

.left-section .name{
  font-size: 3rem;
}
.left-section .job-role{
  font-size: 2rem;
}
.red{
  background-color: grey !important;
}

@media only screen and (max-width:935px){
  .brief-about-me{
    font-size: 1.5rem;
  }
  .salutation{
    font-size: 2.5rem;
  }
  .res-wor-ski .r-w-s{
    width: 75px;
    height: 75px;
    font-size: 1.5rem;
    line-height: 75px;
  }
  .left-section img{
    width: 30rem;
    height: 30rem;
  }
}
@media only screen and (max-width:693px){
  .left-section .name{
    display: none;
  }
  .left-section .job-role{
    display: none;
  }
  .left-section{
    width: 100%;
  }
  .left-section img{
    width: 250px;
  height: 250px;
  margin-top: 6rem;

  }
  .left-section{
    width: 100%;
  }
  .right-section{
    margin-top: 3rem;
    text-align: center;
  }
  .salutation{
    text-align: center;
  }
  .brief-about-me{
    width: 100%;
    text-align: center;
  }
  .res-wor-ski .r-w-s{
  width: 60px;
  height: 60px;
  line-height: 60px;
  font-size: 1.2rem;
  }
}

@media only screen and (max-width:480px){
  .right-section{
    margin-top: 6rem;
    width: 100%;
  }
  .salutation{
  font-size: 2.5rem;
  }
  .brief-about-me{
    font-size: 2rem;
  }
  .res-wor-ski{
    width: 240px;
    margin: auto;
    margin-top: 6rem;
  }
}



